var _self = null;
export default {
	data() {
		return {
			pageCur: 'index'
		};
	},
	onLoad(options) {
		_self = this;
	},
	methods: {
		/**
		 * @param {Object} e
		 * 切换底部导航栏
		 */
		navChange(e) {
			_self.pageCur = e.currentTarget.dataset.cur;
		}
	}
}
